// MapComponent.js
import React, { useState } from 'react';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import styled from 'styled-components';
import Sidebar from './Sidebar';
import ChatComponent from "../Chat/ChatComponent";
import CardsContainer from "./CardsContainer";
import icon from '../../assets/images/icon.svg';

const myIcon = L.icon({
    iconUrl: icon,
    iconSize: [24, 95],
    iconAnchor: [22, 94],
    popupAnchor: [-3, -76],
});

const Container = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin: 60px;
`;

const MapWrapper = styled.div`
  position: relative;
  border-radius: 15px;
  overflow: hidden;
  width: 100%;
  box-sizing: border-box;  
  z-index: 1;
`;

const MapComponent = () => {
    const [points, setPoints] = useState([]);

    const getRandomLatLng = (minLat, maxLat, minLng, maxLng) => {
        const lat = Math.random() * (maxLat - minLat) + minLat;
        const lng = Math.random() * (maxLng - minLng) + minLng;
        return [lat, lng];
    }

    return (
        <div>
            <Container>
                <Sidebar setPoints={setPoints} getRandomLatLng={getRandomLatLng} />
                <MapWrapper>
                    <CardsContainer />
                    <ChatComponent />
                    <MapContainer
                        center={[-17.56666667, -69.50000000]}  // Coordenadas de General Lagos
                        zoom={13}
                        style={{ height: "1200px", width: "100%" }}
                        scrollWheelZoom={false}
                        zoomControl={false}
                    >
                        <TileLayer
                            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                            attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                        />
                        {points.map((point, index) => (
                            <Marker position={point} key={index} icon={myIcon}>
                                <Popup>
                                    Punto en el mapa
                                </Popup>
                            </Marker>
                        ))}
                    </MapContainer>
                </MapWrapper>
            </Container>
        </div>
    );
};

export default MapComponent;
