// CardsContainer.js
import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import Card from './Card';

const CardsWrapper = styled.div`
  position: absolute;
  right: 18px;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end; // Este línea alineará las tarjetas a la derecha.
`;
const getRandomInt = (min, max) => {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
}
const getRandomBool = () => {
    return Math.random() < 0.5;
}

const CardsContainer = () => {
    const [cardData, setCardData] = useState([]);

    useEffect(() => {
        const data = [
            { number: getRandomInt(10000, 100000).toLocaleString('es-ES'), title: "Servicios", percent: getRandomInt(1, 100), change: getRandomBool(), year: "2022" },
            { number: getRandomInt(10000, 100000).toLocaleString('es-ES'), title: "Construidos", percent: getRandomInt(1, 100), change: getRandomBool(), year: "2022" },
            { number: getRandomInt(10000, 100000).toLocaleString('es-ES'), title: "Visitas Nacionales", percent: getRandomInt(1, 100), change: getRandomBool(), year: "2022" },
            { number: getRandomInt(10000, 100000).toLocaleString('es-ES'), title: "Visitas Extranjeros", percent: getRandomInt(1, 100), change: getRandomBool(), year: "2022" },
            { number: getRandomInt(10000, 100000).toLocaleString('es-ES'), title: "Card title 5", percent: getRandomInt(1, 100), change: getRandomBool(), year: "2022" },
        ];

        setCardData(data);
    }, []);

    return (
        <CardsWrapper>
            {cardData.map((card, index) => (
                <Card
                    key={index}
                    number={card.number}
                    title={card.title}
                    percent={card.percent}
                    change={card.change}
                    year={card.year}
                />
            ))}
        </CardsWrapper>
    );
};

export default CardsContainer;