// ChatComponent.js
import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import ChatHeader from './ChatHeader';

// Estilos del componente
const ChatContainer = styled.div`
  position: absolute;
  bottom: 20px;
  right: 20px;
  width: 300px;
  height: ${props => props.isOpen ? '400px' : 'auto'};
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
  z-index: 1000;
  display: flex;
  flex-direction: column;
`;

const MessageContainer = styled.div`
  flex-grow: 1;
  overflow-y: auto;
  padding: 10px;
`;

const UserMessage = styled.div`
  align-self: flex-end;
  background-color: #007bff;
  color: #fff;
  margin: 10px 0;
  padding: 10px;
  border-radius: 8px;
`;

const BotMessage = styled.div`
  background-color: #eee;
  margin: 10px 0;
  padding: 10px;
  border-radius: 8px;
`;

const InputContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-top: 1px solid #eee;
`;

const Input = styled.input`
  flex-grow: 1;
  border: none;
  padding: 10px;
  border-radius: 4px;
`;

const SendButton = styled.button`
  margin-left: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px;
  border-radius: 4px;
  cursor: pointer;
`;

const ChatComponent = () => {
    const [isChatOpen, setChatOpen] = useState(true);
    const [messages, setMessages] = useState([]);
    const [input, setInput] = useState('');

    const handleToggle = () => {
        setChatOpen(!isChatOpen);
    }

    const handleSend = () => {
        setMessages([...messages, { text: input, user: true }]);
        setInput('');
    }

    useEffect(() => {
        if (messages.length && messages[messages.length - 1].user) {
            setTimeout(() => {
                setMessages([...messages, { text: 'Respuesta del bot', user: false }]);
            }, 1000);
        }
    }, [messages]);

    return (
        <ChatContainer isOpen={isChatOpen}>
            <ChatHeader onToggle={handleToggle} isChatOpen={isChatOpen} />
            {isChatOpen && (
                <>
                    <MessageContainer>
                        {messages.map((message, index) => (
                            message.user ?
                                <UserMessage key={index}>You: {message.text}</UserMessage> :
                                <BotMessage key={index}>Bot: {message.text}</BotMessage>
                        ))}
                    </MessageContainer>
                    <InputContainer>
                        <Input
                            placeholder="Escribe tu mensaje..."
                            value={input}
                            onChange={e => setInput(e.target.value)}
                        />
                        <SendButton onClick={handleSend}>Enviar</SendButton>
                    </InputContainer>
                </>
            )}
        </ChatContainer>
    );

};

export default ChatComponent;
