import React from 'react';
import styled from 'styled-components';

const CardWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  //border: 1px solid #ccc;
  border-radius: 8px;
  padding: 10px;
  text-align: right;
  flex: 1;
  margin: 0 10px;
  transition: all 0.3s ease-in-out;
  background-color: ${props => props.theme.colorWhite};
  &:hover {
    transform: scale(1.02);
  }
  color: ${props => props.theme.colorBlueEconomy};
  width: 250px;
  height: 75px;

  &:first-child {
    margin-left: 0;
  }

`;

const Data = styled.h2`
  color: ${props => props.theme.colorBlueEconomy};
  font-weight: 700;
  font-family: ${props => props.theme.titleH4};
  font-size: ${ props => props.theme.fontSize9xl4};
`;

const Title = styled.p`
  color : ${props=> props.theme.colorNeutralGrayish};
  margin-left: 20px;
  line-height: 18px;
  font-size: ${props=> props.theme.body1Size};
  width: 50%; // o cualquier otra medida que se adapte a tu diseño
`;

const StatisticCard = ({ title }) => {
    const data = Math.floor(Math.random() * 1000000);

    return (
        <CardWrapper>
            <Data>{data.toLocaleString('es-ES')}</Data>
            <Title>{title}</Title>
        </CardWrapper>
    );
}

export default StatisticCard;
