// Card.js
import React from 'react';
import styled from 'styled-components';

// Card.js
const CardContainer = styled.div`
  position: relative;
  margin-right: 18px;
  height: 70px;
  background-color: #fff;
  padding: 5px 10px;
  box-sizing: border-box;
  border-radius: 8px;
  box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
  z-index: 1001;
  margin-bottom: 2px;
  margin-top: 18px;
  display: flex;
  align-items: center;
  width: fit-content;  // Este línea permitirá que el ancho se ajuste según el contenido.
`;
const Line = styled.div`
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr 1fr;
  align-items: center;
  justify-items: start;
`;
const Number = styled.span`
  grid-column: 1;
  grid-row: 1 / span 2;
  align-self: center;
  font-family: ${props=>props.theme.titleH4};
  font-style: normal;
  font-weight: 700;
  font-size: ${props=> props.theme.fontSize9xl4};
  line-height: 43px;
  text-align: right;
  color: ${props=> props.theme.colorBlueEconomy};
`;
// Card.js
const Title = styled.h3`
  grid-column: 2;
  grid-row: 1;
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-size: ${props=> props.theme.body1Size};
  line-height: 18px;
`;

const Percent = styled.span`
  grid-column: 2;
  grid-row: 2;
  color: ${props => props.change ? 'green' : 'red'};  // Change color based on change value
`;

const Year = styled.span`
  grid-column: 2;
  grid-row: 2;
  margin-left: auto;
`;


const Card = ({ number, title, percent, change, year }) => {
    return (
        <CardContainer>
            <Line>
                <Number>{number}</Number>
                <Title>{title}</Title>
                <Percent change={change}>{change ? '+' : '-'}{percent}%</Percent>
                <Year>{year}</Year>
            </Line>
        </CardContainer>
    );
};
export default Card;
